class Flexible extends React.Component{
  constructor(props){
    super(props);
    this.state = {img: 1};
  }
  auto = ()=>{
    this.time = setInterval(()=>{
      this.setState({img:this.state.img==7?1:this.state.img +1});
    },2000)
  }
  next = ()=>{
    this.setState({img:this.state.img==7?1:this.state.img +1});
  }
  prev = ()=>{
    this.setState({img:this.state.img==1?7:this.state.img -1})
  }
  pause = ()=>{
    clearInterval(this.time);
  }
  render(){
    return(
      <div className = "parent">
        <h1><i>Beautiful nature</i></h1>
        <button className = "item1" type = "button" onClick = {this.prev}>
          <i className ="fas fa-hand-point-left"></i>
        </button>
        <img src = {"images/image"+this.state.img+".jpg"} alt = "random"/>
        <button className = "item2" type = "button" onClick = {this.next}>
          <i className="fas fa-hand-point-right"></i>
        </button>
        <button className= "item3" onClick = {this.auto}>
          <div class="button">
            <div class="star"></div>
            <div class="star s2"></div>
            <div class=" star s3"></div>
            <span>Auto</span>
          </div>
             <div class="moon"></div>
        </button>
        <button className="stop" type="button" onClick={this.pause}>Stop</button>
      </div>
    )
  }

}
ReactDOM.render(
  <React.StrictMode>
    <Flexible />
  </React.StrictMode>,
  document.getElementById("root")
);
